/**
 * Kullanƒ±cƒ±nƒ±n toplam galibiyet sayƒ±sƒ±na g√∂re rozetini hesapla
 */
export function getUserBadge(totalWins: number): Badge {
    // En y√ºksek e≈üikten ba≈üla, a≈üaƒüƒ± in
    for (let i = BADGES.length - 1; i >= 0; i--) {
        if (totalWins >= BADGES[i].requiredWins) {
            return BADGES[i];
        }
    }

    // Hi√ß galibiyet yoksa bile ilk rozeti ver
    return BADGES[0];
}

/**
 * Sonraki rozete ka√ß galibiyet kaldƒ±ƒüƒ±nƒ± hesapla
 */
export function getWinsToNextBadge(totalWins: number): number | null {
    const currentBadge = getUserBadge(totalWins);
    const currentIndex = BADGES.findIndex(b => b.id === currentBadge.id);

    // Son rozetteyse null d√∂n
    if (currentIndex === BADGES.length - 1) {
        return null;
    }

    const nextBadge = BADGES[currentIndex + 1];
    return nextBadge.requiredWins - totalWins;
}

